import React, { Component } from 'react';
import Terminal from 'terminal-in-react';

class TvTerminal extends React.PureComponent {
  showMsg = () => 'Hello World'

  render() {
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "100vh"
        }}
      >
        <Terminal
          color='#f7f7f7'
          backgroundColor='#626592'
          barColor='#e4e3e5'
          prompt='f7f7f7'
          style={{ fontWeight: "bold", fontSize: "1em",
          font: 'Monaco',
        }}
          commands={
            {
            'whoami': ()=>window.open('https://www.instagram.com/_dhaval._/?hl=en', '_blank'),
            showmsg: this.showMsg,
            popup: () => alert('Terminal in React')
          }}
          descriptions={{
            'whoami':'Gives info about the user',
            showmsg: 'shows a message',
            alert: 'alert', popup: 'alert',
          }
        }
          msg='Hey, this is my personal terminal(under development). Type "help" to get a list of comamnds you can use.'
        />
      </div>
    );
  }
}

export default TvTerminal;


